body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100vw;
  height: 8vh;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  background-color: #000000;
}
.nav-bar {
  width: 90vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.nav-name {
  font-size: 2em;
  color: hotpink;
  border: none;
  background-color: transparent;
}

.nav-btn-bar {
}

.nav-btn {
  font-size: 1.2em;
  color: turquoise;
  border: none;
  background-color: transparent;
}

#landing-main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: left;
          align-items: left;
  background-image: linear-gradient(-200deg, turquoise, white);
  height: 100vh;
  margin-top: -10vh;
}

.pic-info-box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: left;
          justify-content: left;
  -webkit-align-items: center;
          align-items: center;
  height: 50vh;
}
.portrait {
  border: 10px solid hotpink;
  border-radius: 50%;
  height: 25vw;
  margin: auto 10vw auto 20vw;
}

.landing-info-box {
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 20vw;
  width: 20vw;
  color: turquoise;
  background-color: black;
  font-size: 1vw;
}

a {
  color: turquoise;
  text-decoration: none;
}

a:hover {
  color: hotpink;
}

#contact-main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
a {
  height: 3em;
  margin: auto 3vw auto 3vw;
}
img {
  color: white;
  height: 3em;
  margin: auto 3vw auto 3vw;
}

h2 {
  margin: 0;
}

#about-main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 38vh;
  background-color: black;
  padding-top: 10vh;
}

.about-content {
  color: turquoise;
  width: 50vw;
  margin-top: 5vh;
}

#skills-main {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 40vh;
  background-image: linear-gradient(200deg, hotpink, white);
  padding-top: 10vh;
}

.skills-icons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50vw;
  height: 15vw;
}
.row-skills {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.icon-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.icon {
  font-size: 5em;
  color: black;
}

#projects-main {
  height: 90vh;
  background-color: black;
  color: turquoise;
  padding-top: 10vh;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

